import React, { useState } from 'react';
import Button from '@mui/material/Button';
import Container from '@mui/material/Container';
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import DisplayArea from './components/DisplayArea';
import HavenText from './components/HavenText';
import './styles.css';

function App() {
    const [activeButton, setActiveButton] = useState(null);
    const [displayContent, setDisplayContent] = useState(<HavenText />);

    const handleCollect = () => {
        setActiveButton('Collect');
        setDisplayContent(<Typography variant="h4">Success</Typography>);
    };

    const handleRetrieve = () => {
        setActiveButton('Retrieve');
        fetch("http://alb8-2127494217.ap-southeast-2.elb.amazonaws.com/")
            .then(response => response.text())
            .then(data => {
                setDisplayContent(<Typography variant="body1" component="pre">{data}</Typography>);
            })
            .catch(error => setDisplayContent('Error: ' + error));
    };

    const handlePreprocess = () => {
        setActiveButton('Preprocess');
        setDisplayContent(<Typography variant="h4">Preprocessed</Typography>);
    };

    const handleAnalyse = () => {
        setActiveButton('Analyse');
        setDisplayContent(<Typography variant="h4">Analysed</Typography>);
    };

    return (
        <Container>
            <Box display="flex" justifyContent="center" alignItems="center" flexDirection="column" mt={5}>
                <Box display="flex" justifyContent="center" alignItems="center" mb={3}>
                    <Button variant="contained" color={activeButton === 'Collect' ? 'primary' : 'default'} onClick={handleCollect}>Collect</Button>
                    <Typography variant="h6" mx={2}></Typography>
                    <Button variant="contained" color={activeButton === 'Retrieve' ? 'primary' : 'default'} onClick={handleRetrieve}>Retrieve</Button>
                    <Typography variant="h6" mx={2}></Typography>
                    <Button variant="contained" color={activeButton === 'Preprocess' ? 'primary' : 'default'} onClick={handlePreprocess}>Preprocess</Button>
                    <Typography variant="h6" mx={2}></Typography>
                    <Button variant="contained" color={activeButton === 'Analyse' ? 'primary' : 'default'} onClick={handleAnalyse}>Analyse</Button>
                </Box>
                <DisplayArea content={displayContent} />
            </Box>
        </Container>
    );
}

export default App;